"use client";
import { useState, useEffect } from 'react';

export default function TripOverview({ formData, setFormData }) {
  const [localFormData, setLocalFormData] = useState({
    title: '',
    description: '',
    destinationCountry: '',
    destinationType: '',
    destinationSeason: '',
    duration: '',
    images: []
  });

  useEffect(() => {
    if (formData.overview) {
      setLocalFormData(formData.overview);
    }
  }, [formData.overview]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    const updatedLocalData = {
      ...localFormData,
      [name]: value
    };
    setLocalFormData(updatedLocalData);
    setFormData(prev => ({
      ...prev,
      overview: updatedLocalData
    }));
  };

  return (
    <div className="space-y-6">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        <div>
          <label className="block text-sm font-medium text-gray-700 mb-2">
            Trip Title
          </label>
          <input
            type="text"
            name="title"
            value={localFormData.title}
            onChange={handleChange}
            className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-orange-500"
            placeholder="Enter trip title"
          />
        </div>

        <div>
          <label className="block text-sm font-medium text-gray-700 mb-2">
            Duration (days)
          </label>
          <input
            type="number"
            name="duration"
            value={localFormData.duration}
            onChange={handleChange}
            className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-orange-500"
            placeholder="Enter duration in days"
          />
        </div>

        <div>
          <label className="block text-sm font-medium text-gray-700 mb-2">
            Destination Country
          </label>
          <select
            name="destinationCountry"
            value={localFormData.destinationCountry}
            onChange={handleChange}
            className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-orange-500"
          >
            <option value="">Select Country</option>
            <option value="India">India</option>
            <option value="America">America</option>
            <option value="Europe">Europe</option>
            <option value="Australia">Australia</option>
            <option value="Japan">Japan</option>
            <option value="Thailand">Thailand</option>
            <option value="Singapore">Singapore</option>
            <option value="Malaysia">Malaysia</option>
          </select>
        </div>

        <div>
          <label className="block text-sm font-medium text-gray-700 mb-2">
            Destination Type
          </label>
          <select
            name="destinationType"
            value={localFormData.destinationType}
            onChange={handleChange}
            className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-orange-500"
          >
            <option value="">Select Type</option>
            <option value="Beach">Beach</option>
            <option value="Mountain">Mountain</option>
            <option value="Desert">Desert</option>
            <option value="City">City</option>
            <option value="Wildlife">Wildlife</option>
            <option value="Cultural">Cultural</option>
          </select>
        </div>

        <div>
          <label className="block text-sm font-medium text-gray-700 mb-2">
            Best Season
          </label>
          <select
            name="destinationSeason"
            value={localFormData.destinationSeason}
            onChange={handleChange}
            className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-orange-500"
          >
            <option value="">Select Season</option>
            <option value="Summer">Summer</option>
            <option value="Winter">Winter</option>
            <option value="Spring">Spring</option>
            <option value="Autumn">Autumn</option>
            <option value="All Year">All Year</option>
          </select>
        </div>
      </div>

      <div>
        <label className="block text-sm font-medium text-gray-700 mb-2">
          Description
        </label>
        <textarea
          name="description"
          value={localFormData.description}
          onChange={handleChange}
          rows={4}
          className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-orange-500"
          placeholder="Enter trip description"
        />
      </div>

      <div>
        <label className="block text-sm font-medium text-gray-700 mb-2">
          Upload Images
        </label>
        <input
          type="file"
          multiple
          accept="image/*"
          onChange={(e) => {
            const files = Array.from(e.target.files || []);
            const imageUrls = files.map(file => URL.createObjectURL(file));
            handleChange({
              target: {
                name: 'images',
                value: imageUrls
              }
            });
          }}
          className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-orange-500"
        />
      </div>
    </div>
  );
}